/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */
/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";
/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import "~@ionic/angular/css/display.css";
/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";
@font-face {
  font-family: "nexa_bold";
  src: url("../assets/fonts/nexa_bold.otf");
}
@font-face {
  font-family: "mons_regular";
  src: url("../assets/fonts/nexa_light.otf");
}
ion-app.cameraView,
ion-app.cameraView ion-content,
ion-app.cameraView .nav-decor {
  background: transparent none !important;
}

ion-datetime {
  overflow: scroll;
}

.date-container {
  font-size: 10px;
  font-family: "nexa_bold";
  color: #ffffff;
  display: flex;
  text-align-last: end;
  font-weight: bolder;
}

.card-md {
  margin: 10px;
  border-radius: 2px;
  width: calc(100% - 20px);
  font-size: 1.4rem;
  background: #fff;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.card-md ion-card-header ion-row ion-col {
  padding: 0;
}

.chemicaltest-modal {
  --height: 35%;
  --border-radius: 16px;
  --width: 80%;
}

.toast_notification .toast-wrapper.toast-top {
  background-color: rgba(0, 0, 0, 0.6);
}

.toast_notification .toast-message {
  color: white;
  font-weight: bolder;
  margin-left: 40px;
  z-index: 1;
}

.notification-card {
  --background: transparent;
}
.notification-card ion-card {
  border-radius: 15px;
  background-color: rgba(31, 31, 31, 0.8);
}

.toast_notification .toast-button {
  opacity: 1;
  position: absolute;
  background-image: url("../src/assets/imgs/icon.png");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #f5f5f5;
  left: 2px;
  margin: 1px;
  border: none;
}

ion-icon[class*=prefix-] {
  mask-size: contain;
  mask-position: 50% 50%;
  mask-repeat: no-repeat;
  background: currentColor;
  margin: 7px;
  width: 1em;
  height: 1em;
}
ion-icon[class*=prefix-report] {
  mask-image: url(../src/assets/icon/report.svg);
}
ion-icon[class*=prefix-hand] {
  mask-image: url(../src/assets/icon/hand.svg);
}
ion-icon[class*=prefix-farmer] {
  mask-image: url(../src/assets/icon/farmer.svg);
}
ion-icon[class*=prefix-edpu] {
  mask-image: url(../src/assets/icon/edpu.svg);
}
ion-icon[class*=prefix-home] {
  mask-image: url(../src/assets/icon/home.svg);
}
ion-icon[class*=prefix-weighing] {
  mask-image: url(../src/assets/icon/weighing.svg);
}
ion-icon[class*=prefix-doubleArrow] {
  mask-image: url(../src/assets/icon/double_arrow.svg);
}
ion-icon[class*=prefix-money2] {
  mask-image: url(../src/assets/icon/money2.svg);
}

#notification-btn {
  position: relative;
}

#notification-badge {
  position: absolute;
  top: 5px;
  right: -5px;
  background-color: #fff;
  color: #5d9e2f;
  border-radius: 40%;
  font-size: x-small;
}

.popover-content {
  top: 45px !important;
}

.flexcenter {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.profile_pic_center {
  align-content: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.center_item {
  display: flex;
  align-items: center;
}

.gridAlt1 {
  background-color: #ffffff;
  color: #635f5f;
}

.gridAlt2 {
  background-color: #f5f5f5;
  color: #635f5f;
}

.gridAlt3 {
  background-color: #e6ffe6;
  color: #635f5f;
}

.center_content {
  text-align: center;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.flex_center_content {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.block_center_content {
  display: block;
  text-align: center;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.border_content {
  border: 1px solid black;
}

.ionRefresherStyles {
  top: 90px !important;
}

/* general classes */
.web-container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 1rem;
}

.page-heading-color {
  color: #000000;
}

.page-heading {
  margin: 0;
}

.row2 {
  display: flex;
  align-items: center;
}

.row-right {
  flex-shrink: 0;
  margin-left: auto;
}

.ion-fab-btn {
  background-color: rgba(0, 0, 0, 0) !important;
  color: #5d9e2f;
  box-shadow: none !important;
}

.priceProfile-row-item {
  margin: 1rem auto;
  border: 2px solid rgba(0, 0, 0, 0.1882352941) !important;
}

.report-table {
  width: 100%;
  overflow: hidden;
  border-collapse: separate !important;
  border-radius: 0.6rem;
  margin-top: 2rem;
  box-shadow: 0px -2px 10px 1px rgba(0, 0, 0, 0.1450980392);
}

.report-table tbody tr:nth-child(odd) {
  background-color: #efffe3;
  border-radius: 0.6rem;
}

.report-table__row__error {
  background-color: rgba(255, 0, 0, 0.2509803922) !important;
}

.report-table__row__error:nth-child(odd) {
  background-color: rgba(255, 0, 0, 0.1882352941) !important;
}

.report-list {
  margin-top: 1rem;
}

.report-list-item {
  border-radius: 0.6rem;
  box-shadow: 0px 3px 10px 1px rgba(0, 0, 0, 0.1254901961);
  padding: 1.2rem;
  padding-bottom: 0px;
  margin: 2rem auto;
}

.report-table__head {
  background-color: #5d9e2f;
  color: white;
  border-top-left-radius: 0.6rem;
  border-top-right-radius: 0.6rem;
}

.report-table td {
  padding: 1rem;
  color: #4b4b4b;
}

.report-table th {
  padding: 1rem;
}

.report-table tr:first-of-type {
  border-top-left-radius: 0.6rem;
  border-top-right-radius: 0.6rem;
}

.report-table th:first-of-type {
  border-top-left-radius: 0.6rem;
}

.report-table__item-options__button {
  cursor: pointer;
  padding: 5px 10px;
}

.report-table__item-options__button:hover {
  color: black;
}

.report-table__item-options__menu {
  position: absolute;
  border: 1px solid black;
  z-index: 5;
}

.show-when-mobile,
.show-when-mobile-840,
.show-when-mobile-1050 {
  display: none !important;
}

.rowOnDesktop {
  display: flex;
}

.search-bar {
  border: 3px solid #74b743;
  width: 100%;
  max-width: 28rem;
  border-radius: 0.6rem;
  position: relative;
  height: 37px;
  margin-left: 1rem;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.search-bar__input {
  padding-right: 63px;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 4px;
}

.search-bar > .search-bar__input:disabled {
  border-color: rgba(116, 183, 67, 0.1882352941);
}

.search-bar__input:disabled {
  opacity: 0.3;
}

.search-bar__btn {
  position: absolute;
  top: -3px;
  right: -3px;
  height: 94%;
  box-sizing: content-box;
  width: 50px;
  background: #74b743 !important;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  color: #fff;
  border: 3px solid #74b743;
}

.search-bar__btn:hover {
  background-color: #68a43c !important;
  border: 3px solid #68a43c !important;
}

.search-bar__btn:active {
  background-color: #5c9235 !important;
  border: 3px solid #5c9235 !important;
}

.moreOptions {
  position: absolute;
  top: 0;
  right: 0;
  background-color: white;
  z-index: 10002;
  width: auto;
  border: 1px solid #999;
  box-shadow: 0px 1px 10px 0px #999;
}

.card-list-container {
  display: grid;
  grid-template-columns: auto auto;
  align-items: start;
}

.card-list-container-one-col {
  display: grid;
  grid-template-columns: auto;
  align-items: start;
}

.card-list-container-three-col {
  display: grid;
  grid-template-columns: repeat(3, minmax(auto, 520px));
  justify-items: center;
  align-items: start;
}

.login-form__container {
  border-radius: 6rem;
  padding: 3rem;
  box-shadow: 0px 10px 20px 0px rgba(72, 138, 255, 0.1254901961);
  max-width: 420px;
}

.login_input {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  min-height: 40px;
  border: 4px solid rgba(255, 255, 255, 0);
  border-radius: 25px;
  background-color: rgba(30, 255, 0, 0.1490196078);
  padding: 1.3rem 2.2rem;
  font-size: 15px;
  transition: 200ms all linear;
}

.login_input:focus {
  outline: none;
  background-color: rgba(255, 255, 255, 0);
  border: 4px solid rgba(136, 221, 75, 0.5019607843);
}

.login_form__showPasswordBtn {
  outline: none;
  background: none !important;
  box-shadow: none;
  color: #88dd4b;
}

.dispatch-summary-row__mobile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  margin-bottom: 10px;
}

.web-container__header-right {
  margin-left: auto;
}

/* media queries */
@media only screen and (max-width: 1050px) {
  .remove-when-mobile-1050 {
    display: none;
  }
  .show-when-mobile-1050 {
    display: block !important;
  }
  .row-col {
    flex-direction: column;
    align-items: center;
  }
  .priceProfileMaster-buttons__right {
    margin-left: 0px;
  }
  .web-container__header-right {
    margin-left: 0 !important;
  }
}
@media only screen and (max-width: 840px) {
  .remove-when-mobile-840 {
    display: none;
  }
  .search-bar {
    margin-left: 0;
  }
  .show-when-mobile-840 {
    display: block !important;
  }
}
@media only screen and (max-width: 670px) {
  .remove-when-mobile {
    display: none;
  }
  .show-when-mobile {
    display: block !important;
  }
  .rowOnDesktop {
    flex-direction: column !important;
    justify-content: center !important;
  }
  .login-form__container {
    border-radius: 0rem;
    padding: 0rem;
    border: 0px;
    box-shadow: none;
    max-width: 320px;
    margin-bottom: 5rem;
  }
  .login-form__inputsAndButtons {
    margin-top: 6rem;
  }
}
@media only screen and (max-width: 840px) {
  .card-list-container {
    grid-template-columns: auto;
  }
  .scroll-when-mobile {
    max-height: 60vh;
    overflow: scroll;
  }
}
@media only screen and (max-width: 420px) {
  .search-bar {
    max-width: 100%;
  }
}