.notification-card {
  ion-card {
    border-radius: 15px;
    background-color: rgba(31, 31, 31, 0.8);
  }
  --background: transparent;
}

.toast_notification .toast-button {
  opacity: 1;
  position: absolute;
  background-image: url("../src/assets/imgs/icon.png");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #f5f5f5;
  left: 2px;
  margin: 1px;
  border: none;
}

ion-icon {
  &[class*="prefix-"] {
    mask-size: contain;
    mask-position: 50% 50%;
    mask-repeat: no-repeat;
    background: currentColor;
    margin: 7px;
    width: 1em;
    height: 1em;
  }
  // custom icons

  &[class*="prefix-report"] {
    mask-image: url(../src/assets/icon/report.svg);
  }
  &[class*="prefix-hand"] {
    mask-image: url(../src/assets/icon/hand.svg);
  }
  &[class*="prefix-farmer"] {
    mask-image: url(../src/assets/icon/farmer.svg);
  }
  &[class*="prefix-edpu"] {
    mask-image: url(../src/assets/icon/edpu.svg);
  }
  &[class*="prefix-home"] {
    mask-image: url(../src/assets/icon/home.svg);
  }
  &[class*="prefix-weighing"] {
    mask-image: url(../src/assets/icon/weighing.svg);
  }
  &[class*="prefix-doubleArrow"] {
    mask-image: url(../src/assets/icon/double_arrow.svg);
  }
  &[class*="prefix-money2"] {
    mask-image: url(../src/assets/icon/money2.svg);
  }
}
