/* general classes */

.web-container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 1rem;
}

.page-heading-color {
  color: #000000;
}

.page-heading {
  margin: 0;
}

.row2 {
  display: flex;
  align-items: center;
}

.row-right {
  flex-shrink: 0;
  margin-left: auto;
}

.ion-fab-btn {
  background-color: #00000000 !important;
  color: #5d9e2f; //#098dda; Ramee
  box-shadow: none !important;
}

.priceProfile-row-item {
  // padding-left: 0;
  margin: 1rem auto;
  border: 2px solid #00000030 !important;
}

.report-table {
  // border: 1px solid black;
  width: 100%;
  overflow: hidden;
  border-collapse: separate !important;
  border-radius: 0.6rem;
  margin-top: 2rem;
  box-shadow: 0px -2px 10px 1px #00000025;
}
.report-table tbody tr:nth-child(odd) {
  background-color: #efffe3;
  border-radius: 0.6rem;
}

.report-table__row__error {
  background-color: #ff000040 !important;
}

.report-table__row__error:nth-child(odd) {
  background-color: #ff000030 !important;
}

.report-list {
  margin-top: 1rem;
}

.report-list-item {
  border-radius: 0.6rem;
  box-shadow: 0px 3px 10px 1px #00000020;
  padding: 1.2rem;
  padding-bottom: 0px;
  margin: 2rem auto;
}
// .report-list-item-row1{

// }
// .report-list-item-row2{

// }

.report-table__head {
  background-color: #5d9e2f; // #098dda; Ramee
  color: white;
  border-top-left-radius: 0.6rem;
  border-top-right-radius: 0.6rem;
}

.report-table td {
  padding: 1rem;
  color: #4b4b4b;
}

.report-table th {
  padding: 1rem;
}

.report-table tr:first-of-type {
  border-top-left-radius: 0.6rem;
  border-top-right-radius: 0.6rem;
}
.report-table th:first-of-type {
  border-top-left-radius: 0.6rem;
  // border-top-right-radius: 0.6rem;
}

.report-table__item-options__button {
  cursor: pointer;
  padding: 5px 10px;
  // background-color: red;
}

.report-table__item-options__button:hover {
  // fill: black;
  color: black;
}

.report-table__item-options__menu {
  position: absolute;
  border: 1px solid black;
  z-index: 5;
}

.show-when-mobile,
.show-when-mobile-840,
.show-when-mobile-1050 {
  display: none !important;
}

.rowOnDesktop {
  display: flex;
}

.search-bar {
  border: 3px solid #74b743;
  width: 100%;
  max-width: 28rem;
  border-radius: 0.6rem;
  position: relative;
  height: 37px;
  margin-left: 1rem;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.search-bar__input {
  padding-right: 63px;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 4px;
}

.search-bar > .search-bar__input:disabled {
  border-color: #74b74330;
}

.search-bar__input:disabled {
  opacity: 0.3;
}

.search-bar__btn {
  position: absolute;
  top: -3px;
  right: -3px;
  height: 94%;
  box-sizing: content-box;
  width: 50px;
  background: #74b743 !important; //#74b743 Ramee
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  color: #fff;
  border: 3px solid #74b743; //#74b743; Ramee
}
.search-bar__btn:hover {
  // background-color: darken(#74b743, $amount: 5) !important;
  background-color: darken(#74b743, $amount: 5) !important;
  //border: 3px solid darken(#74b743, $amount: 5) !important;
  border: 3px solid darken(#74b743, $amount: 5) !important;
}

.search-bar__btn:active {
  // background-color: darken(#74b743, $amount: 10) !important;
  //border: 3px solid darken(#74b743, $amount: 10) !important;
  background-color: darken(#74b743, $amount: 10) !important;
  border: 3px solid darken(#74b743, $amount: 10) !important;
}

.moreOptions {
  position: absolute;
  top: 0;
  right: 0;
  background-color: white;
  z-index: 10002;
  width: auto;
  border: 1px solid #999;
  box-shadow: 0px 1px 10px 0px #999;
}

.card-list-container {
  display: grid;
  grid-template-columns: auto auto;
  // justify-items: center;
  align-items: start;
}

.card-list-container-one-col {
  display: grid;
  grid-template-columns: auto;
  // justify-items: center;
  align-items: start;
}

.card-list-container-three-col {
  display: grid;
  grid-template-columns: repeat(3, minmax(auto, 520px));
  justify-items: center;
  align-items: start;
}

.login-form__container {
  border-radius: 6rem;
  padding: 3rem;
  // border:4px solid #009df940;
  box-shadow: 0px 10px 20px 0px #488aff20;
  max-width: 420px;
}

.login_input {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  min-height: 40px;
  border: 4px solid #ffffff00;
  border-radius: 25px;
  background-color: #1eff0026;
  padding: 1.3rem 2.2rem;
  font-size: 15px;
  transition: 200ms all linear;
}

.login_input:focus {
  outline: none;
  background-color: #ffffff00;
  border: 4px solid #88dd4b80;
}

.login_form__showPasswordBtn {
  outline: none;
  background: none !important;
  box-shadow: none;
  color: #88dd4b;
}
.dispatch-summary-row__mobile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  margin-bottom: 10px;
}

.web-container__header-right {
  margin-left: auto;
}

/* media queries */

@media only screen and (max-width: 1050px) {
  .remove-when-mobile-1050 {
    display: none;
  }
  .show-when-mobile-1050 {
    display: block !important;
  }
  .row-col {
    flex-direction: column;
    align-items: center;
  }

  .priceProfileMaster-buttons__right {
    margin-left: 0px;
  }
  .web-container__header-right {
    margin-left: 0 !important;
  }
}

@media only screen and (max-width: 840px) {
  .remove-when-mobile-840 {
    display: none;
  }

  .search-bar {
    margin-left: 0;
  }
  .show-when-mobile-840 {
    display: block !important;
  }
}

@media only screen and (max-width: 670px) {
  .remove-when-mobile {
    display: none;
  }
  .show-when-mobile {
    display: block !important;
  }
  .rowOnDesktop {
    flex-direction: column !important;
    justify-content: center !important;
  }

  .login-form__container {
    border-radius: 0rem;
    padding: 0rem;
    border: 0px;
    box-shadow: none;
    max-width: 320px;
    margin-bottom: 5rem;
  }

  .login-form__inputsAndButtons {
    margin-top: 6rem;
  }
}

@media only screen and (max-width: 840px) {
  .card-list-container {
    grid-template-columns: auto;
  }
  .scroll-when-mobile {
    max-height: 60vh;
    overflow: scroll;
  }
}
@media only screen and (max-width: 420px) {
  .search-bar {
    max-width: 100%;
  }
}
